import React from 'react'
import Affair from './Affair'
import {AffairType, FilterType} from './HW2'
import s from "./Affairs.module.css"
import SuperButton from "../h4/common/c2-SuperButton/SuperButton";

type AffairsPropsType = { // need to fix any
    data: AffairType[]
    setFilter: (priority: FilterType) => void
    deleteAffairCallback: (_id: number) => void
}

function Affairs(props: AffairsPropsType) {
    const mappedAffairs = props.data.map((a: AffairType) => (
        <div>
        <tr key={a._id}>
            <th className={s.th}>{a._id}.</th>
            <Affair // should work
                key={a._id} // –∫–µ–∏ –û–ë–Ø–ó–ê–¢–ï–õ–¨–ù–´ –≤ 99% - —Ç–∞–∫ —á—Ç–æ –ª—É—á—à–µ –∏—Ö –ø–∏—Å–∞—Ç—å –≤—Å–µ–≥–¥–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –≤ –º–∞–ø–µ
                affair={a}
                deleteAffairCallback={props.deleteAffairCallback}
            />
        </tr>
        </div>
    ))

    const setAll = () => {
        props.setFilter('all')
    } // need to fix
    const setHigh = () => {
        props.setFilter('high')
    }
    const setMiddle = () => {
        props.setFilter('middle')
    }
    const setLow = () => {
        props.setFilter('low')
    }

    return (
        <>
            {mappedAffairs}
            <div className={s.buttonContainer}> {/*–≤—Å–µ —ç—Ç–∏ div—ã –∏ –∫–Ω–æ–ø–∫–∏ –≤—ã–∑—ã–≤–∞—é—Ç –æ—à–∏–±–∫–∏ –≤ –∫–æ–Ω—Å–æ–ª–∏*/}
                <SuperButton onClick={setAll}>All</SuperButton>
                <SuperButton onClick={setHigh}>High</SuperButton>
                <SuperButton onClick={setMiddle}>Middle</SuperButton>
                <SuperButton onClick={setLow}>Low</SuperButton>
            </div>
        </>
    )
}

export default Affairs
